  @import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic&family=Noto+Kufi+Arabic:wght@300&display=swap');

:root{
  --maing : #98D894;
  --mainb : #9DBDE0;
}
body{
  background-color: var(--maing);
  padding: 12px;
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}
::-webkit-scrollbar{
  width: 10px;
}
::-webkit-scrollbar-track{
  width: 10px;
  background-color: var(--maing);
}
::-webkit-scrollbar-thumb{
  width: 10px;
  background-color: var(--mainb);
}
